import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';
import { Observable, Subject } from 'rxjs';
import { IMedioDePago } from '../models/medio-de-pago';
import { AuthService } from '@core/auth/auth.service';
import { Response } from '@shared/models/response-with-token';

@Injectable()
export class MediosDePagoService {
    constructor(private http: HttpClient, private _authService:AuthService) { }
    /**
     * Funcion en el servicio
     * - Esta funcion retorna los Rubros.
     * @returns Rubros
     */
    getClientes(sort?:any): Observable<Response[]> {
        let f = '?';
        f += sort == 'desc' ? 'sort=-descripcion' : 'sort=descripcion';
        let subject = new Subject<any>();

        this.http.get<Response[]>(`${environment.administracion.view_clientes}${f}`)
        .subscribe((resp:any) => {
            this._authService.accessToken = resp.token,
            subject.next({
                'list':resp.result,
                // 'totalPages': resp.headers.get('x-pagination-page-count'),
                // 'totalCount': resp.headers.get('x-pagination-total-count')
            })
        });
        return subject.asObservable();
    }

    /**
     * Funcion en el Servicio 
     * @param medio_de_pago 
     * @returns Crea un item nuevo
     */
    saveMedioDePago(medio_de_pago: IMedioDePago): Observable<IMedioDePago> {
        return this.http.post<IMedioDePago>(``, medio_de_pago);
    }

    /**
     * Funcion en el servicio
     * @param medio_de_pago
     * @returns Item actualizado actualizado
     */
    updateMedioDePago(medio_de_pago: IMedioDePago): Observable<IMedioDePago> {
        return this.http.put<IMedioDePago>(`` + '/' + medio_de_pago.idpagotipo, medio_de_pago);
    }

    /**
     * Funcion en el servicio
     * @param idpagotipo 
     * @returns Elimina un item
     */
    deleteMedioDePago(idpagotipo: number): Observable<IMedioDePago> {
        return this.http.delete<IMedioDePago>(`` + '/' + idpagotipo);
    }
} 