import { Component, Inject, OnInit } from '@angular/core';
import { IMedioDePago } from '../../models/medio-de-pago';
import { MediosDePagoService } from '../../service/medios-de-pago.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Subject, takeUntil } from 'rxjs';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
    selector: 'app-save-medios-de-pago',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss']
})
export class SaveComponent implements OnInit {
    columns = ['nombre', 'activo', 'acciones'];
    form: FormGroup;
    medio_de_pago: IMedioDePago;
    title: string;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _mediosDePagoService: MediosDePagoService,
        private _empresaService: EmpresaService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.medio_de_pago = this.data ? this.data : null;
        this.title = this.medio_de_pago?.idpagotipo ? 'Editar medio de pago' : 'Nuevo medio de pago';
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
        
        // Load empresa data
        this._empresaService.getEmpresa();
        this.newForm();
    }

    newForm() {
        this.form = new FormGroup({
            descripcion: new FormControl((this.medio_de_pago ? this.medio_de_pago.descripcion : ''), Validators.required),
            activo: new FormControl((this.medio_de_pago ? this.medio_de_pago.activo : ''), Validators.required),
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            this.medio_de_pago = {
                ...this.medio_de_pago,
                descripcion: this.form.get('descripcion').value,
                activo: this.form.get('activo').value,
            }
            if (this.medio_de_pago.idpagotipo) {
                this._mediosDePagoService.updateMedioDePago(this.medio_de_pago)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            } else {
                this._mediosDePagoService.saveMedioDePago(this.medio_de_pago)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close()
    }
}